//START - SERVICES TILE - SECTION 1
@mixin services_tile--section-1 {
	max-width: 425px;
	height: 440px;

	&.-express-send > .img {
		max-width: 361px;
		transform: rotate(-30deg) translate(-32%, -93px);
	}

	&.-bank-transfer {
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% -30%;

		> .description {
			max-width: max-content;
		}
	}

	&.-pay-qr {
		> .description {
			max-width: none;
		}

		> .img {
			max-width: 427.8px;
			transform: translate(-10%);
			bottom: -8px;
		}
	}

	&.-send-many {
		@include services_tile--section-1--send-many;
	}

	&.-gcash-padala {
		max-width: 915px;
		padding-top: 69px;

		> .description {
			max-width: 20ch;
		}

		> .img {
			max-width: 806px;
			transform: translate(-36.5%, -36%);
		}
	}

	&.-send-clip {
		@include services-tile--section-1--send-clip;
	}
}

// SERVICES TILE - SECTION 1 - SEND CLIP
@mixin services-tile--section-1--send-clip {
	> .header > .title {
		max-width: 7ch;
		margin-top: $spacing-secondary;
	}

	> .description {
		max-width: 16ch;
	}

	> .img {
		max-width: 338px;
		transform: translate(-6%, -55.7%);
	}
}

//SERVICES TILE - SECTION 1 - SEND MANY
@mixin services_tile--section-1--send-many {
	> .description {
		max-width: max-content;
	}

	> .img {
		&.-blue-box {
			max-width: 193px;
			transform: translate(-88%, 22%);
		}

		&.-pink-box {
			max-width: 134px;
			transform: translate(32%, -16%);
		}
	}
}

//SERVICES TILE - LARGE DESKTOP - SECTION 1
@mixin services_tile--large-desktop--section-1 {
	max-width: 303px;
	height: 350px;

	&.-express-send > .img {
		transform: rotate(-30deg) translate(-10%, -38px);
	}

	&.-bank-transfer {
		background-position-y: -300%;
	}

	&.-bank-transfer > .description,
	&.-send-many > .description {
		max-width: 18ch;
	}

	&.-pay-qr > .img {
		max-width: 299px;
		transform: translate(0);
		bottom: -75px;
	}

	&.-send-many > .img {
		&.-blue-box {
			max-width: 129.23px;
			transform: translate(-94%, 11%);
		}

		&.-pink-box {
			max-width: 89.72px;
			transform: translate(25%, -42%);
		}
	}

	&.-gcash-padala {
		max-width: 655px;
		padding-top: 51px;

		> .img {
			max-width: 513px;
			transform: translate(-24%, -49%);
		}
	}

	&.-send-clip {
		> .header > .title {
			margin-top: $spacing-default;
		}

		> .description {
			max-width: 11ch;
		}

		> .img {
			max-width: 227px;
			transform: translate(-1%, -76%);
		}
	}
}

//SERVICES TILE - SMALL DESKTOP - SECTION 1 - GCASH PADALA
@mixin services_tile--small-desktop--section-1--gcash-padala {
	max-width: 514px;
	padding-top: 30px;

	> .header {
		display: block;

		> .title {
			margin-top: $spacing-default;
		}
	}

	> .description > span {
		white-space: nowrap;
	}

	> .img {
		max-width: 362px;
		transform: translate(-21%, -57%);
	}
}

//SERVICES TILE - SMALL DESKTOP - SECTION 1
@mixin services_tile--small-desktop--section-1 {
	max-width: 238px;
	height: 250px;

