//START SERVICES TILE - SECTION 2
@mixin services_tile--section-2 {
	max-width: 674px;
	height: 440px;
	padding: 102px 0 0 40px;
	box-shadow: 0px 42px 17px rgba(6, 50, 111, 0.01),
		0px 23px 14px rgba(6, 50, 111, 0.02), 0px 10px 10px rgba(6, 50, 111, 0.03),
		0px 3px 6px rgba(6, 50, 111, 0.04), 0px 0px 0px rgba(6, 50, 111, 0.04),
		0px 4px 10px rgba(0, 0, 0, 0.05);

	> .header > .title {
		margin-top: $spacing-secondary;
	}

	> .description {
		max-width: max-content;
	}

	&.-load {
		@include services_tile--section-2--load;
	}

	&.-transportation {
		> .description {
			max-width: 25ch;
		}

		> .img {
			max-width: 560px;
			transform: translate(-219px, -278px);
		}
	}

	&.-bills {
		@include services_tile--section-2--bills;
	}
}

//START SERVICES TILE - SECTION 2 - LOAD
@mixin services_tile--section-2--load {
	background: radial-gradient(
			92.5% 143.77% at 100% 6.36%,
			rgba(246, 249, 253, 0) 0%,
			#cce9ff 100%
		),
		linear-gradient(180deg, #edf7fe 36.68%, #daeefa 100%),
		radial-gradient(
			65.48% 132.18% at 75.67% 43.57%,
			#1d3f96 0%,
			#1d3e95 6.67%,
			#1c3e94 13.33%,
			#1c3d92 20%,
			#1b3c8f 26.67%,
			#1b3a8b 33.33%,
			#1a3886 40%,
			#193681 46.67%,
			#18347c 53.33%,
			#173176 60%,
			#162f72 66.67%,
			#152e6e 73.33%,
			#142d6b 80%,
			#142c68 86.67%,
			#142b67 93.33%,
			#142b67 100%
		);
	background-blend-mode: multiply, normal, normal, normal;
	box-shadow: 0px 42px 17px rgba(6, 50, 111, 0.01),
		0px 23px 14px rgba(6, 50, 111, 0.02), 0px 10px 10px rgba(6, 50, 111, 0.03),
		0px 3px 6px rgba(6, 50, 111, 0.04), 0px 0px 0px rgba(6, 50, 111, 0.04);

	> .img {
		&.-money {
			max-width: 470px;
			transform: translate(-3%, -55%);
			z-index: 2;
		}

		&.-mobile {
			max-width: 310px;
			transform: translate(52%, -31%);
			z-index: 1;
			mix-blend-mode: multiply;
		}
	}
}

