//SERVICES TILE
.services_tile {
	width: 100%;
	padding: 40px 0 0 40px;
	position: relative;
	overflow: hidden;
	border-radius: 20px;
	box-shadow: 0px 42px 17px rgba(6, 50, 111, 0.01),
		0px 23px 14px rgba(6, 50, 111, 0.02), 0px 10px 10px rgba(6, 50, 111, 0.03),
		0px 3px 6px rgba(6, 50, 111, 0.04), 0px 0px 0px rgba(6, 50, 111, 0.04),
		0px 8px 12px -8px rgba(15, 37, 69, 0.2);

	> .header {
		> .icon {
			max-width: 52px;
		}

		> .title {
			font: 700 2rem/1.3 $gotham-bold;
			letter-spacing: -0.03em;
			margin-top: $spacing-default;
		}
	}

	> .description {
		font: 500 1.14rem/1.4 $gilroy-medium;
		margin-top: $spacing-default;
		max-width: 12ch;
	}

	> .link {
		display: flex;
		font: 700 1rem/1.5 $gilroy-bold;
		margin-top: $spacing-default;

		> .icon {
			margin-left: $spacing-half;
		}
	}

	> .img {
		width: 100%;
		position: absolute;
		left: 50%;
	}

	&.-section-1 {
		@include services_tile--section-1;
	}

	&.-section-2 {
		@include services_tile--section-2;
	}

	&.-small {
		@include services-tile--small;
	}

	@include on(large-desktop) {
		@include services_tile--large-desktop;
	}

	@include on(small-desktop) {
		@include services_tile--small-desktop;
	}

	@include on(tablet) {
		@include services_tile--tablet;
	}

	@include on(mobile) {
		@include services_tile--mobile;
	}

	@include on(small-mobile) {
		@include services_tile--small-mobile;
	}
}

