	padding: 120px 0 260px;

	.section-6-img-1 {
		transform: translate(-104%, -2%);
	}

	.section-6-img {
		&.pin-1 {
			transform: translate(-711%, -384%);
		}

		&.pin-2 {
			transform: translate(-414%, -134%);
		}

		&.pin-3 {
			transform: translate(-856%, 70%);
		}

		&.pin-4 {
			transform: translate(-433%, 263%);
		}
	}
}

@mixin get-started_panel-6--small-desktop {
	padding: 80px 0;

	.section-6-img-1 {
		transform: translate(-104%, 5%);
	}

	.section-6-img {
		width: 50px;
		height: 73px;

		&.pin-1 {
			transform: translate(-711%, -322%);
		}

		&.pin-2 {
			transform: translate(-411%, -57%);
		}

		&.pin-3 {
			transform: translate(-854%, 146%);
