	.get-started_wrapper-bounding-box > .content {
		.get-started_content-block.-outlets {
			padding: 0 0 $spacing-quad;
		}

		.get-started_section-card.-section-6 {
			margin-top: $spacing-double;
		}
	}

	.get-started_wrapper-bounding-box > .content:not(:first-of-type) {
		margin: 0 auto;

		.get-started_section-card.-section-6 {
			margin-top: 607px;

			&:last-of-type {
				margin-top: $spacing-double;
			}
