	}

	.section-6-img-1 {
		width: 350px;
		height: 600.83px;
		transform: translate(-50%, -16%);
	}

	.section-6-img {
		width: 36.75px;
		height: 53.2px;

		&.pin-1 {
			transform: translate(-145%, -550%);
		}

		&.pin-2 {
			transform: translate(127%, -298%);
		}

		&.pin-3 {
			transform: translate(-283%, -84%);
		}

		&.pin-4 {
			transform: translate(116%, 116%);
		}
