		margin: 0 auto;

		.get-started_content-block.-outlets {
			padding: 0 0 $spacing-triple;
		}

		&:not(:first-of-type) .get-started_section-card.-section-6 {
			margin-top: 491px;
		}
	}

	.section-6-img-1 {
		width: 295px;
		height: 490.37px;
		transform: translate(-50%, -27%);
	}

	.section-6-img {
		width: 31px;
		height: 45px;

