}

@mixin get-started_panel-6--medium-mobile {
	.section-6-img-1 {
		transform: translate(-50%, -29%);
	}

	.section-6-img {
		&.pin-1 {
			transform: translate(-142%, -678%);
		}

		&.pin-2 {
			transform: translate(129%, -434%);
		}

		&.pin-3 {
			transform: translate(-275%, -224%);
		}

		&.pin-4 {
			transform: translate(111%, -34%);
		}
	}

	.get-started_wrapper-bounding-box > .content {
		&:not(:first-of-type) .get-started_section-card.-section-6 {
			margin-top: 504px;
