@mixin get-started_panel-6--small-mobile {
	.section-6-img-1 {
		transform: translate(-50%, -45%);
	}

	.section-6-img {
		&.pin-1 {
			transform: translate(-142%, -850%);
		}

		&.pin-2 {
			transform: translate(129%, -604%);
		}

		&.pin-3 {
			transform: translate(-277%, -396%);
		}

		&.pin-4 {
			transform: translate(111%, -207%);
