.download-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;

    > .btn {
        width: 119px;
        height: 54px;
        border-radius: 50px;
        background: #6FBAF7;
        display: flex;
        align-items: center;
        justify-content: center;
        font: 700 0.86rem/1.4 $gilroy-bold;
        color: white;
    }

    > .title {
        margin-top: $spacing-secondary;
        font: $headline-section;
    }

    > .description {
        margin-top: $spacing-double;
        font: $content-large;
    }
}